<!-- è½¬ç -->
<template>
  <div class="big-wheel-wrap" :style="{backgroundImage: `url('${backgroundImage}')`}">
    <div class="big-wheel" ref="bigWheel" :style="{width, height}">
      <div class="wrap" :style="{transform: rotateAngle, transition: rotateTransition}">
        <canvas ref="canvas">æµè§å¨çæ¬è¿ä½</canvas>
        <div class="prize-wrap">
          <div
            class="item"
            v-for="(item,index) in prizeList"
            :key="index"
            :style="_calcRotateAngle(index)">
            <slot name="items" :item="item"/>
          </div>
        </div>
      </div>
    </div>
    <!-- ä¸­é´æé®é¨å -->
    <slot>
      <img class="start-btn" @click="start" :src="startBtnImage" />
    </slot>
  </div>
</template>

<script>
export default {
  name: 'BigWheel',
  props: {
    prizeList: { // å¥ååè¡¨
      type: Array,
      required: true
    },
    width: {
      required: true
    },
    height: {
      required: true
    },
    prizeBgColors: { // æ¯ä¸åæå½¢çèæ¯è²ï¼å¾ªç¯å¡«å
      type: Array,
      default: () => ['#F47F45', '#FFA468']
    },
    borderColor: { // æ¯ä¸åæå½¢çå¤è¾¹æ¡é¢è²
      type: String,
      default: '#ff9800'
    },
    turnsNumber: { // è½¬å¨çåæ°
      type: Number,
      default: 5
    },
    turnsTime: { // è½¬å¨æç»­æ¶é´ï¼ç§ï¼
      type: Number,
      default: 5
    },
    backgroundImage: { // å¤åèæ¯å¾ç
      required: false,
      default: require('@/assets/img/disk_bg.png')
    },
    startBtnImage: { // å¼å§æé®å¾ç
      required: false,
      default: require('@/assets/img/go.png')
    }
  },
  data () {
    return {
      isLock: false, // ç¦æ­¢éå¤ç¹å»
      startRotateDegree: 0, // å¼å§è½¬å¨çè§åº¦
      rotateAngle: 'rotate(0deg)', // è®¾ç½®æéé»è®¤æåçä½ç½®,ç°å¨æ¯é»è®¤æå1ä¸ªæå½¢ä¸­é´ï¼è®¾ç½®ä¸º0å³æåä¸¤ä¸ªæå½¢ä¹é´ççº¿ä¸
      rotateTransition: ''
    }
  },
  methods: {
    // åå§åcanvas
    init () {
      const prizeNum = this.prizeList.length
      const prizeBgColors = this.prizeBgColors
      const borderColor = this.borderColor
      // ç»ç»
      const canvas = this.$refs.canvas
      const ctx = canvas.getContext('2d')
      const canvasW = this.$refs.canvas.width = this.$refs.bigWheel.clientWidth // ç»æ¿çé«åº¦
      const canvasH = this.$refs.canvas.height = this.$refs.bigWheel.clientHeight // ç»æ¿çå®½åº¦

      // translateæ¹æ³éæ°æ å°ç»å¸ä¸ç (0,0) ä½ç½®
      ctx.translate(0, canvasH)

      // rotateæ¹æ³æè½¬å½åçç»å¾ï¼å ä¸ºæå­æ¯åå½åæå½¢ä¸­å¿çº¿åç´ç
      ctx.rotate(-90 * Math.PI / 180)

      // åç¯çå¤åçåå¾,å¯ç¨æ¥è°æ´åçå¤§å°æ¥éåºå¤é¨çå­çå¤§å°
      const outRadius = canvasW / 2 - 1

      const innerRadius = 0 // åç¯çååçåå¾

      const baseAngle = Math.PI * 2 / prizeNum // æ¯ä¸ªå¥é¡¹æå è§åº¦æ°

      ctx.clearRect(0, 0, canvasW, canvasH) // å»æèæ¯é»è®¤è²

      ctx.strokeStyle = borderColor // è®¾ç½®ç»å¾çº¿çé¢è²

      let prizeBgColorsIndex = -1
      for (let index = 0; index < prizeNum; index++) {
        const angle = index * baseAngle

        // è®¾ç½®æ¯ä¸ªæå½¢åºåçèæ¯è²ï¼å¾ªç¯å¡«å
        prizeBgColorsIndex++
        ctx.fillStyle = prizeBgColors[prizeBgColorsIndex]
        if (prizeBgColorsIndex === prizeBgColors.length - 1) {
          prizeBgColorsIndex = -1
        }

        ctx.beginPath() // å¼å§ç»å¶
        // æ ååå¼§ï¼arc(x,y,radius,startAngle,endAngle,anticlockwise)
        ctx.arc(canvasW * 0.5, canvasH * 0.5, outRadius, angle, angle + baseAngle, false)
        ctx.arc(canvasW * 0.5, canvasH * 0.5, innerRadius, angle + baseAngle, angle, true)
        ctx.stroke()
        ctx.fill()
        ctx.save()
      }
    },
    /**
     * åçè½¬å¨æ¹æ³
     * @param {number} index - ä¸­å¥çå¥ååºå·
     */
    rotate (index) {
      const turnsTime = this.turnsTime
      const rotateAngle = this.startRotateDegree + this.turnsNumber * 360 + 360 - (180 / this.prizeList.length + 360 / this.prizeList.length * index) - this.startRotateDegree % 360
      this.startRotateDegree = rotateAngle
      this.rotateAngle = `rotate(${rotateAngle}deg)`
      this.rotateTransition = `transform ${turnsTime}s cubic-bezier(0.250, 0.460, 0.455, 0.995)`
      setTimeout(() => {
        // è§¦åç»æï¼è¿åå¯¹è±¡
        this.$emit('over', this.prizeList[index])
        this.isLock = false
      }, turnsTime * 1000 + 500)
    },
    // æ ¹æ®indexè®¡ç®æ¯ä¸ä¸ªå¥åæå½¢è¦æè½¬çè§åº¦æ ·å¼
    _calcRotateAngle (index) {
      const angle = 360 / this.prizeList.length * index + (180 / this.prizeList.length)
      return {
        transform: `rotate(${angle}deg)`
      }
    },
    start () {
      if (this.isLock) return
      this.isLock = true
      // è§¦åå¼å§
      this.$emit('start')
    }
  },
  mounted () {
    this.init()
  }
}

</script>
<style lang="scss" scoped>

.big-wheel-wrap {
  position: absolute;
  top: 1.7rem;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  font-size: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  padding: .22rem;

  .start-btn {
    position: absolute;
    top: 1.19rem;
    left: 50%;
    transform: translateX(-50%);
    width: .8rem;
  }
}

.big-wheel {
  position: relative;
  display: inline-block;

  .wrap {
    position: absolute;
    width: 100%;
    height: 100%;

    .prize-wrap {
      position: absolute;
      left: 25%;
      top: 0;
      width: 50%;
      height: 50%;

      .item {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        transform-origin: center bottom;
      }
    }
  }
}
</style>
